<template>
  <div class="landing">
    <SearchForm @searchedGame="searchGame($event)"></SearchForm>
    <div class="d-block d-md-none">
    <LandingButtons
              :upcoming="upcoming"
              :highlights="isHighlight"
              :featured="isFeatured">
    </LandingButtons>
    </div>
    
    <div class="card rounded-0">
      <div class="landing__main card-body p-0">
        <highlights v-show="isHighlight"></highlights>
        <featured v-show="isFeatured"></featured>
        <upcoming v-show="upcoming"></upcoming>
      </div>
    </div>

  </div>
</template>

<script>
  const Highlights = () => import('../Games/HighlightedGames.vue');
  const Upcoming = () => import('../Games/UpcomingGames.vue');
  const Featured = () => import('../Games/FeaturedGames.vue');
  const SearchForm = () => import('./../Utilities/SearchForm.vue');
  const LandingButtons = () => import("../Utilities/LandingButtons");
  export default {
    components: {SearchForm, Highlights, Upcoming, Featured, LandingButtons},
    data () {
      return {
        upcoming: false,
        isHighlight: true,
        isFeatured: false,
        noHighlightedGames: false,
        noFeaturedGames: false,
        daySelected: 'today'
      }
    },
    mounted () {
      this.$EventBus.$on('gameComponentChanged', (component) => {
        this.switchComponents(component);
      });
    },
    methods: {
        switchGamesByDate (event) {
            this.$EventBus.$emit('dateChanged', event)
        },
      switchComponents (component) {
          if (component === 'highlihgts') {
            this.isHighlight = true;
            this.upcoming = false;
            this.isFeatured = false;
          } else if (component === 'featured') {
            this.isFeatured = true;
            this.upcoming = false;
            this.isHighlight = false;
          } else if (component === 'upcoming') {
            this.upcoming = true;
            this.isFeatured = false;
            this.isHighlight = false;
          } else {
            this.isHighlight = true;
            this.upcoming = false;
            this.isFeatured = false;
          }
      },
    }
  }
</script>

<style scoped>
  .btn-group > button:focus {
    outline: none;
  }
</style>
